  color: #333;
  text-decoration: none;
  transition: all 0.3s;
}

.c-pagetop a::before {
  border-top: 2px solid #333;
  border-right: 2px solid #333;
  position: absolute;
  color: #fff;
  top: 1.125rem;
  right: 0.625rem;
  left: 0.625rem;
  width: 1.25rem;
  height: 1.25rem;
  margin: auto;
  content: "";
  transform: rotate(-45deg);
  transition: all 0.3s;
}

.c-pagetop a:hover {
