  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  transition: 0.3s ease-in-out;
  z-index: -1;
}

.c-button:hover {
  color: #333;
  transition: 0.3s ease-in-out;
}

.c-button:hover::before {
  transform: translateX(0);
  transition: 0.3s ease-in-out;
}

